
<template>
  	<div>
		<h2 class="public-title">
			<template v-if="transferUser">{{transferUser}} - </template>{{english.交易记录}}
			<a v-show="transferId" href="javascript:void(0)" class="go-bank" @click="goBank">{{english.返回}}</a>
		
		</h2>
		<div class="search-wrapper clearfix">
			<div class="search-item item-auto">
				<span>{{english.时间}}：</span>
				<!-- <span v-show="beginTime == '' && endTime == ''" style="margin-right:10px;">全部</span> -->
			</div>
			<div class="search-item item-DatePicker">
				<DatePicker :options="startOption" v-model="beginTime" ref="beginTime" :placeholder="english.开始时间"></DatePicker>
				<DatePicker :options="startOption" v-model="endTime" ref="endTime" :placeholder="english.结束时间"></DatePicker>
			</div>
			<div class="search-item"><Button type="error" @click="search">{{english.搜索}}</Button></div>
			<div class="search-item trading">
				<span class="text">|</span>
				<span :class="msg == 1 ? 'cur' : ''" @click="_getDate( 0, 'DD', 1)">{{english.今天}}</span>
				<span :class="msg == 2 ? 'cur' : ''" @click="_getDate(-1, 'DD', 2)">{{english.昨天}}</span>
				<span :class="msg == 3 ? 'cur' : ''" @click="_getDate( 7, 'DD', 3)">{{english.本周}}</span>
				<span :class="msg == 4 ? 'cur' : ''" @click="_getDate( 0, 'MM', 4)">{{english.本月}}</span>
				<span :class="msg == 5 ? 'cur' : ''" @click="_getDate(-1, 'MM', 5)">{{english.上月}}</span>
			</div>
		</div>
		<div class="screen-wrapper clearfix" v-show="typeList.length || statusList.length">
			<ul>
				<li class="screen-list">
					<div class="name">{{english.分类}}：</div>
					<!-- <div class="all"><span :class="{'active' : tranType == 0}" @click="_lotteryType(0)">全部</span></div> -->
					<div class="list">
						<span :class="{'active' : tranType == item.id}" v-for="(item, i) in typeList" :key="i" @click="_lotteryType(item.id)">{{english[item.name]}}</span>
					</div>          
				</li>
				<li class="screen-list">
					<div class="name">{{english.状态}}：</div>
					<!-- <div class="all"><span :class="{'active' : tranStatus == 0}" @click="_tradingStatus(0)">全部</span></div> -->
					<div class="list">
						<span :class="{'active' : tranStatus == 0}" @click="_tradingStatus(0)">{{english.全部}}</span>
						<span :class="{'active' : tranStatus == item.id}" v-for="(item, i) in statusList" :key="i" @click="_tradingStatus(item.id)">{{english[item.name]}}</span>
					</div>
				</li>
			</ul>
		</div>
      	<!-- 表格部分 -->
     	<Table class="table-wrapper" stripe :columns="tableData" :data="listData" :loading="listLoading"></Table>

		<div class="singleMoney-wrapper" v-if="listData.length && singleMoney.length">
			<span v-for="(item, index) in singleMoney" :key="index">
				{{item.name}}：<em class="text-red">{{item.money}}</em>
			</span>
		</div>
		
		<div class="page-wrapper clearfix" v-show="listData.length">
			<Page :total="pageCount" @on-change="_getPage(page)" :current.sync="page"></Page>
		</div>
    </div>
</template>
<script>
import formatDate from '../../../common/js/date.js';
import dateSelection from '@/common/js/dateSelection.js';
import english from '../../../assets/json/English.json';

export default {
	props: {
        transferId: { //数据
            type: String,
            default: '',
		},
		transferUser: { //数据
            type: String,
            default: '',
        },
        recordFlag: {
            type: Boolean,
            default: false,
        }
    },
    data() {
		return {
			startOption: {
				disabledDate (date) {
					return date && date.valueOf() > Date.now()
				}
			},
			english: english,
			msg: -1,
			beginTime: '', //开始时间
			endTime: '', //结束时间
			tranType: 0, //交易类型
			tranStatus: 0, //交易状态
			pageCount: 10, //分页数（10等于一页）
			page: 1, //分页
			listLoading: false, //列表数据加载
			typeList: [], //彩种列表
			statusList: [], //状态列表
			singleMoney: [], //金额统计
			listData: [], //列表数据
			tableData: [
				{
					title: english.时间,
					key: 'addtime'
				},{
					title: english.交易金额,
					// key: 'money'
					render: function(h, params) {
						return h("div", [
							h("span", {
								class: params.row.money_type == 2 ? 'text-green' : 'text-red'
							}, (params.row.money_type == 2 ? '-' : '+') + params.row.money)
						]);
					}
				},{
					title: english.类型,
					key: 'name'
				},{
					title: english.状态,
					key: 'status'
				},{
					title: english.备注,
					key: 'order_no'
				},
			],
		}
    },
    created() {
		if (this.$route.query.tranType) { //交易类型传参
			this.tranType = this.$route.query.tranType;
		}
		// this.resetAll();
		this._getDate( 0, 'DD', -1);
    },
    methods: {
		goBank() {
			this.$emit("recordHide", false)
		},
		_getTimes() { //初始化时间
			this.beginTime = this.$refs.beginTime.visualValue;
			this.endTime = this.$refs.endTime.visualValue;
		},
		search() { //搜索
			let vm = this;
			vm.msg = -1;
			vm.page = 1;
			vm._getTimes();
			if (vm.beginTime > vm.endTime) {
				vm.$Message.warning(english.结束时间不能小于开始时间);
				return false;
			}
			vm._getListData();
		},
		_getDate(num, string, index) { //筛选时间
			let vm = this;
			vm.msg = index;
			vm.page = 1;
			if (num == 7) { //获取本周
				let date = new Date();
				let timestamp = date.getTime(); //当天时间戳
				let weekday = date.getDay();
				vm.beginTime = formatDate(new Date(timestamp - 3600 * 1000 * 24 * weekday), 'yyyy-MM-dd');
				vm.endTime = formatDate(new Date(timestamp), 'yyyy-MM-dd');
			} else {
				let newtime = dateSelection(num, string);
				vm.beginTime = formatDate(new Date(newtime.beginTime), 'yyyy-MM-dd');
				vm.endTime = formatDate(new Date(newtime.endTime), 'yyyy-MM-dd');
			}
			vm._getListData();
		},
		_getPage(page) {
			let vm = this;
			vm.page = page;
			vm._getTimes();
			vm._getListData();
		},
		resetAll() {
			let vm = this;
			vm.msg = -1;
			vm.beginTime = '';
			vm.endTime = '';
			vm.tranType = 0;
			vm.tranStatus = 0;
			vm.page = 1;
			vm._getListData();
		},
		_lotteryType(id) {
			let vm = this;
			vm.tranType = id;
			vm.page = 1;
			vm._getTimes();
			vm._getListData();
		},
		_tradingStatus(id) {
			let vm = this;
			vm.tranStatus = id;
			vm.page = 1;
			vm._getTimes();
			vm._getListData();
		},
		_getListData() { //列表页数据
			let vm = this;
			let params = {};
				params.token = localStorage.getItem('token');
				params.start_time = vm.beginTime;
				params.end_time = vm.endTime;
				params.type = vm.tranType;
				params.status = vm.tranStatus;
				params.page = vm.page;
				if (vm.transferId) {
					params.user_id = vm.transferId;
					params.source = 2
				}
			vm.listLoading = true;
			vm.$http.post(vm.urlRequest + "?m=api&c=account&a=getBills", params).then(res => {
				if (res.data.status == 0) {
					vm.typeList = res.data.trantype;
					vm.statusList = res.data.transtatus;
					vm.singleMoney = res.data.single_money;
					vm.listData = res.data.list;
					vm.listData = vm.listData.map(function(list) {
						list.name = english[list.name];
						list.status = english[list.status];
						return list;
					})	
					vm.pageCount = res.data.pageNum * 10;
				} else {
					if (res.data.ret_msg != '') {
						this.$Message.warning(res.data.ret_msg);
					}
				}
				vm.listLoading = false;
			}).catch(err => {
				vm.listLoading = false;
			})
		},
    },
    watch: {     
      
    }
}
</script>
<style scoped>
.go-bank {
	float: right;
	margin-top: 13px;
    cursor: pointer;
    padding: 0 15px;
    font-size: 12px;
	line-height: 27px;
    font-weight: 400;
	color: #fff;
    background-color: #2d8cf0;
	border: 1px solid transparent;
    border-color: #2d8cf0;
	border-radius: 4px;
}
.go-bank:hover {
	background-color: #57a3f3;
    border-color: #57a3f3;
}
.search-wrapper {
	display: flex;
	justify-content: flex-start;
	margin: 20px 0;
	width: 100%;
	height: auto;
	line-height: 32px;
}
.search-wrapper .search-item {
	margin-right: 10px;
}
.search-wrapper .item-auto {
	width: auto;
	margin-right: 0;
}
.search-wrapper .item-DatePicker {
	width: 300px;
	display: flex;
	justify-content: space-between;
}
.search-wrapper .item-DatePicker >>> .ivu-date-picker {
	width: 48%;
}
.search-wrapper .trading span {
	display: inline-block;
	margin: 4px 0;
	padding: 0 5px;
	line-height: 24px;
	cursor: pointer;
}
.search-wrapper .trading .text:hover {
  	color: red;
}
.search-wrapper .cur {
	color: #fff;
	background: #ed3f14;
	border-radius: 10%;
}

.screen-wrapper {
	padding: 10px 0;
    border-top: 1px solid #eee;
}
.screen-list {
	margin: 15px 0;
	display: flex;
	justify-content: flex-start;
	line-height: 24px;
}
.screen-list .name {
	flex: 0 0 50px;
	width: 50px;
}
.screen-list .all {
	flex: 0 0 50px;
	width: 50px;
}
.screen-list .list {
	flex: 1;
	margin-top: -8px;
}
.screen-list .list span {
	margin-right: 12px;
	margin-top: 8px;
}
.screen-list span {
    display: inline-block;
    padding: 0 5px;
	cursor: pointer;
}
.screen-list span:hover {
	color: #ed3f14;
}
.screen-list span.active {
	color: #fff;
	background: #ed3f14;
	border-radius: 10%;
}

.singleMoney-wrapper {
	margin: 20px 0;
	padding: 8px 12px;
	width: 100%;
    font-size: 14px;
    background: rgba(0,0,0,.01);
	border: 1px solid #eee;
}
.singleMoney-wrapper span {
	margin-right: 12px;
	display: inline-block;
}
.page-wrapper {
	margin: 20px 0;
	width: 100%;
	text-align: center;
}
.table-wrapper >>> .text-red,
.text-red {
    color: #D22727;
}
.table-wrapper >>> .text-green,
.text-green {
    color: #34B86C;
}
</style>



